<template>
  <v-container  v-bind:style="{background: fondo3}">
        <h1  v-bind:style="{color: colorTexto, fontSize: textSize*1.5 + 'px', fontFamily: textFam, background: fondo3}">LUGARES DE INTERS</h1>
        <br>
        <v-container fluid>
        <v-row dense>
            <v-col
            v-for="card in cards"
            :key="card.title"
            v-bind:style="{color: colorTexto, fontSize: textSize + 'px', fontFamily: textFam}"
            :cols="card.flex"
            >
            <v-card>
                <v-img
                    :src="card.src"
                    class="white--text align-end"
                    gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)"
                    height="300px"
                >
                </v-img>
                <div class="d-flex flex-no-wrap justify-space-between">
                    <div>
                        <v-card-title v-text="card.title"></v-card-title>
                    </div>
                    <div>
                        <v-card-actions>     
                            <v-btn
                                icon
                                @click="card.show= !card.show"
                            >
                                <v-icon>{{ card.show ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
                            </v-btn>
                        </v-card-actions>
                    </div>
                </div>
                    <v-expand-transition>
                        <div v-show="card.show">
                            <v-divider></v-divider>
                            <v-card-text v-text="card.descr"></v-card-text>
                        </div>
                    </v-expand-transition>

            </v-card>
            </v-col>
        </v-row>
        </v-container>
  </v-container>
</template>

<script>
  export default {
    data: () => ({
        drawer: false,
        group: null,
        fondo1: "#64B5F6", 
        fondo2: "#90CAF9", 
        fondo3: "#BBDEFB",
        colorFondoTexto: "",
        tipoLetra: "",
        descr: "",
        informacion: false,
        textSize: 20,
        textFam: "Helvetica",
        colorTexto: "white",
        fondoTexto: "transparent",
        cards: [
            { title: 'CATEDRAL', src: 'http://upload.wikimedia.org/wikipedia/commons/7/76/Catedral_de_leon.jpg', flex: 6, show: false, descr: 'La catedral de Santa Mara de Regla de Len es un templo de culto catlico, sede episcopal de la dicesis de Len, Espaa, consagrada bajo la advocacin de la Virgen Mara. Fue el primer monumento declarado en Espaa mediante Real Orden de 28 de agosto del ao 1844 (confirmada por Real Orden el 24 de septiembre del ao 1845).'},
            { title: 'CASA BOTINES', src: 'https://c2.staticflickr.com/8/7620/17020863832_ca8a36118e_b.jpg', flex: 6, show: false, descr: 'La casa Botines o casa Fernndez y Andrs es un edificio de estilo modernista situado en la ciudad espaola de Len, capital de la provincia homnima. Originalmente era un almacn comercial y residencia particular. Construido y diseado por el arquitecto Antoni Gaud entre 1891 y 1892, es una de sus tres obras fuera de Catalua, junto al Palacio Episcopal de Astorga tambin en la provincia de Len y El Capricho de Comillas, en Cantabria.' },
            { title: 'SAN MARCOS', src: 'https://media-cdn.tripadvisor.com/media/photo-s/02/83/a3/6d/exterior-parador.jpg', flex: 6, show: false, descr: 'El convento de San Marcos es una de las grandes joyas de la arquitectura de la ciudad espaola de Len junto con la catedral, la baslica de San Isidoro y la Casa Botines. Se encuentra hoy convertido en parador de turismo de Espaa, adems de iglesia consagrada y, antiguamente, Museo de Len, siendo uno de los monumentos ms importantes del Renacimiento espaol.' },
            { title: 'BASLICA DE SAN ISIDORO', src: 'https://c2.staticflickr.com/8/7233/7220650042_142556a823_b.jpg', flex: 6, show: false, descr: 'La Real Colegiata Baslica de San Isidoro o, simplemente, San Isidoro de Len, es un templo cristiano ubicado en la ciudad de Len, en Espaa. Es uno de los conjuntos arquitectnicos de estilo romnico ms destacados de Espaa, por su historia, arquitectura, escultura, y por los objetos suntuarios romnicos que se han podido conservar. Presenta la particularidad de tener un Panten Real ubicado a los pies de la iglesia, con pintura mural romnica y capiteles originales, todo lo cual hace que sea pieza nica del mundo romnico de la poca. El conjunto fue construido y engrandecido durante los siglos xi y xii' },
            { title: 'PALACIO DE LOS GUZMANES', src: 'https://upload.wikimedia.org/wikipedia/commons/3/34/Fachada_del_Palacio_de_los_Guzmanes.jpg', flex: 6, show: false, descr: 'El palacio de los Guzmanes es un palacio renacentista del siglo xvi situado en la plaza de San Marcelo junto a la Casa Botines en la ciudad de Len, Espaa. Su traza se debe al maestro Rodrigo Gil de Hontan,1 si bien se ocup de su ejecucin Juan de Ribero Rada.2:321 A pesar de quedar inconcluso se convirti en el palacio ms destacado de la ciudad. Ya en el siglo xx, la Diputacin Provincial de Len se hizo cargo de terminarlo para albergar sus nuevas oficinas, funcin que desempea hasta la actualidad.'}, 
            { title: 'UNIVERSIDAD DE LEN', src: 'https://www.fmoinfluencer.es/wp-content/uploads/2019/05/Foto-Campus-ULE-Le%C3%B3n.jpg', flex: 6, show: false, descr: 'Universidad pblica con sede en Len, (Espaa), y con un campus adicional en Ponferrada. El germen de la universidad lo encontramos en 1843, cuando se crea la Escuela Normal de Maestros o Seminario de Maestros de Instruccin Pblica y en la escuela subalterna de Veterinaria, fundada en 1852, sentando las bases de la futura universidad leonesa. Fue fundada en 1979 como escisin de la Universidad de Oviedo, a partir de las diversas Escuelas y Facultades que, dependientes de aquella.'},
            { title: 'MUSAC', src: 'https://aegeegames.files.wordpress.com/2013/04/musac.jpg', flex: 12, show: false, descr: 'El Museo de Arte Contemporneo de Castilla y Len, tambin conocido por sus siglas MUSAC, est situado en la ciudad de Len (Espaa). Creado con el propsito de ser un "Museo del Presente" o "Museo del siglo XXI", su coleccin y sus exposiciones y actividades estn fundamentalmente dirigidas a abarcar el arte actual en sus diferentes manifestaciones.'}, 
        ],
    
    }),
    
      /////////////////////
     //     MOUNTED     //
    /////////////////////
     mounted(){
      console.log("ESTOY ENN MOUNTED");
      console.log(this.$route.query.colorTexto);
      this.textSize = this.$route.query.textSize;
      this.textFam = this.$route.query.textFam;
      this.colorFondoTexto = this.$route.query.colorFondoTexto;
    },

      /////////////////////
     //      WATCH      //
    /////////////////////
    watch:{
      
      /****** CAMBIAR TIPOGRAFA ******/
      textFam:function() {
        if(this.textFam == "Por defecto"){
            this.textFam = "Helvetica";
        }
      },

      /****** CAMBIAR COLORES ******/
      colorFondoTexto:function() {
        switch(this.colorFondoTexto){
          case "Negro sobre Blanco":
            this.fondo1 = "#F5F5F5";
            this.fondo2 = "#FAFAFA"; 
            this.fondo3 = "#FFFFFF";
            this.colorTexto = "black";
            break;
          case "Blanco sobre Negro":
            this.fondo1 = "#212121"; 
            this.fondo2 = "#424242";
            this.fondo3 = "#616161";
            this.colorTexto = "white";
            break;
          case "Negro sobre Amarillo":
            this.fondo1 = "#FFEE58"; 
            this.fondo2 = "#FFF176";
            this.fondo3 = "#FFF9C4";
            this.colorTexto = "black";
            break;
          case "Amarillo sobre Negro":
            this.colorTexto = "yellow";
            this.fondo1 = "#212121"; 
            this.fondo2 = "#424242";
            this.fondo3 = "#616161";
            break; 
          case "Por defecto":
          default:
            this.fondoTexto = "transparent";
            this.colorTexto = "black";
            this.fondo1 = "#64B5F6"; 
            this.fondo2 = "#90CAF9";
            this.fondo3 = "#BBDEFB";
            break;
        }
      }
    },

      /////////////////////
     //     MTODOS     //
    /////////////////////
    methods:{
        informacion(descr){
            setTimeout(() =>{
                descr = !descr;
            }, 1000);
        }
    }
  }
    
</script>